/* FadeInFadeOut */
.fade-in-fade-out-enter {
  opacity: 0;
}

.fade-in-fade-out-enter.fade-in-fade-out-enter-active {
  opacity: 1;
  -webkit-transition: 700ms ease;
  -o-transition: 700ms ease;
  transition: 700ms ease;
}

.fade-in-fade-out-exit {
  opacity: 1;
}

.fade-in-fade-out-exit.fade-in-fade-out-exit-active {
  opacity: 0;
  -webkit-transition: 350ms ease;
  -o-transition: 350ms ease;
  transition: 350ms ease;
}

/* TranslateDown */
.translate-down-enter {
  opacity: 0;
  -webkit-transform: translateY(-30px);
      -ms-transform: translateY(-30px);
          transform: translateY(-30px);
}

.translate-down-enter.translate-down-enter-active {
  opacity: 1;
  -webkit-transition: 700ms ease;
  -o-transition: 700ms ease;
  transition: 700ms ease;
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);
}

/* TranslateRight */
.translate-right-enter {
  opacity: 0;
  -webkit-transform: translateX(-30px);
      -ms-transform: translateX(-30px);
          transform: translateX(-30px);
}

.translate-right-enter.translate-right-enter-active {
  opacity: 1;
  -webkit-transition: 700ms ease;
  -o-transition: 700ms ease;
  transition: 700ms ease;
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);
}


/* TranslateLeft */
.translate-left-enter {
  opacity: 0;
  -webkit-transform: translateX(30px);
      -ms-transform: translateX(30px);
          transform: translateX(30px);
}

.translate-left-enter.translate-left-enter-active {
  opacity: 1;
  -webkit-transition: 700ms ease;
  -o-transition: 700ms ease;
  transition: 700ms ease;
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);
}

/* TranslateLeftReport */
.translate-left-report-enter {
  opacity: 1;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

.translate-left-report-enter.translate-left-report-enter-active {
  opacity: 0;
  -webkit-transition: 350ms ease;
  -o-transition: 350ms ease;
  transition: 350ms ease;
  -webkit-transform: scale(0.5);
      -ms-transform: scale(0.5);
          transform: scale(0.5);
}

/* TranslateRightReport */
.translate-right-report-enter {
  opacity: 0;
  -webkit-transform: scale(0.5);
      -ms-transform: scale(0.5);
          transform: scale(0.5);
}

.translate-right-report-enter.translate-right-report-enter-active {
  opacity: 1;
  -webkit-transition: 350ms ease;
  -o-transition: 350ms ease;
  transition: 350ms ease;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

/* TranslateLeftProfile */
.translate-left-profile-enter {
  -webkit-transform: translateX(190px);
      -ms-transform: translateX(190px);
          transform: translateX(190px);
}

.translate-left-profile-enter.translate-left-profile-enter-active {
  -webkit-transition: 350ms ease;
  -o-transition: 350ms ease;
  transition: 350ms ease;
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);
}

/* TranslateRightProfile */
.translate-right-profile-enter {
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);
}

.translate-right-profile-enter.translate-right-profile-enter-active {
  -webkit-transition: 350ms ease;
  -o-transition: 350ms ease;
  transition: 350ms ease;
  -webkit-transform: translateX(190px);
      -ms-transform: translateX(190px);
          transform: translateX(190px);
}


